import React from "react";
import style from "./HeaderMain.module.css";
import { Link } from "react-scroll";

const HeaderMain = () => {
  return (
    <div className={style.main}>
      <p className={style.titleBold}>  </p>
      <p className={style.titleRegular}> 18 2  </p>
      <p className={style.descr}>
           {" "}
        <span className={style.descrDecor}></span> {" "}
        <span className={style.descrDecor}></span>   
        !
      </p>
      <Link
        className={style.buttonScroll}
        to="feedback"
        spy={true}
        smooth={true}
        duration={400}
      >
        <button type="button" className={style.btnMore}>
          
        </button>
      </Link>
    </div>
  );
};

export default HeaderMain;
