import React from "react";
import style from "./CostContactForm.module.css";
import NumberFormat from "react-number-format";

const CostContactForm = ({
  userName,
  userPhone,
  nameEmpty,
  phoneEmpty,
  handleChange
}) => {
  return (
    <div className={style.contacts}>
      <div className={style.inputOverlay}>
        <input
          className={style.input}
          type="text"
          name="userName"
          id=""
          value={userName}
          placeholder=" "
          onChange={handleChange}
        />
        {(userName && <p className={style.placeholder}> </p>) ||
          (nameEmpty && !userName && (
            <p className={style.placeholderEmpty}>  </p>
          ))}
      </div>
      <div className={style.inputOverlay}>
        <NumberFormat
          className={style.input}
          type="tel"
          format="+38 (###) ###-##-##"
          mask="_"
          name="userPhone"
          value={userPhone}
          placeholder=" "
          onChange={handleChange}
        />
        {(phoneEmpty && (
          <p className={style.placeholderEmpty}>  </p>
        )) ||
          (userPhone && <p className={style.placeholder}> </p>)}
      </div>
      <button type="submit" className={style.button}>
         
      </button>
    </div>
  );
};

export default CostContactForm;
